import React, { useState } from "react"; 
 
function Ex1(){ 
    
    //let count = 0;
    //      setter
    const [count ,setCount] = useState(0);

    function btnClick(){
        //alert('Clicked');
        //count++;
        //console.log(count);
        //setCount count-> rendering()
        setCount(count+1);
        
    }
    
    return ( 
        <div>
             1 : { count }<br/>
             <input type="button" value="1" onClick={ btnClick } /> &nbsp;
             <input type="button" value="2" onClick={ function(){ setCount(count+2) } } />&nbsp;
             <input type="button" value="3" onClick={ ()=> setCount(count+3) } />
             <br/>
             2 : { count }<br/>
        </div> 
    ); 
} 
export default Ex1;