/* eslint-disable */ 
import React, { useState } from "react"; 
 
function Ex2(){ 

    const [txt,setTxt] = useState("");

    function setChange(e){
       //console.log(e.target.value);
       // txt   (rendering)
       setTxt(e.target.value);
    }

    return ( 
        <div>
            : <input  value={txt}  onChange={ setChange }  /> <br/>
            : { txt }

        </div> 
    ); 
} 
export default Ex2;